import { useDataMutation } from '@dhis2/app-runtime';
import { usePoll } from '../../hooks/use-poll';
const pollQuery = {
  resource: 'system/taskSummaries/DATA_INTEGRITY',
  id: ({
    jobId
  }) => jobId
};
const startDataIntegrityCheckMutation = {
  resource: 'dataIntegrity',
  type: 'create'
};
export const useDataIntegrity = () => {
  const poll = usePoll({
    query: pollQuery,
    interval: 3000,
    checkDone: data => data
  });
  const [startDataIntegrityCheck, {
    loading,
    error,
    data
  }] = useDataMutation(startDataIntegrityCheckMutation, {
    onComplete: data => {
      const {
        id: jobId
      } = data.response;
      poll.start({
        jobId
      });
    }
  });
  const isPolling = data && !poll.data;
  return {
    startDataIntegrityCheck,
    loading: loading || isPolling,
    error: error || poll.error,
    issues: poll.data
  };
};